.allcars {
    width: 100%;
    height: 140vh;
    margin-bottom: 2rem;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;

}

.allcars h1 {
    background: linear-gradient(to top left, #800000, #ff5733);
    -webkit-background-clip: text;
    color: transparent;
}

.allcars span h3 {
    color: grey;
}

.allcars-container {
    width: 80%;
    height: 75%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}

.allcarscard {
    width: 30%;
    height: 45%;
    border: none;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    margin: 1rem;
    align-items: center;
    background-color: white;
    border-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

.allcarscard:hover {
    transform: scale(1.1);
    transition: 0.3s ease-out;
}

.allcars-1 {
    width: 80%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.allcars-1 img {
    width: 100%;
    height: auto;
    object-fit: contain;
}

.allcars-2 {
    width: 100%;
    height: 60%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
}

.allcars-2 h3 {
    font-size: 1.2rem;
    background: linear-gradient(to top left, #800000, #ff5733);
    -webkit-background-clip: text;
    color: transparent;
}

.allcars-2 p {
    color: gray;
}

.allcars-2 h2 {
    font-weight: lighter;
}

.pagination {
    width: 100%;
    height: 10vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.allcars-button {
    cursor: pointer;
    width: 2.5rem;
    height: 2.5rem;
    font-size: 1rem;
    border: none;
    margin: 0.5rem;
    border-radius: 50%;
}

.allcars-button:hover {
    transform: scale(1.1);
    transition: 0.3s ease-out;
}

@media screen and (max-width:600px) {
    .allcars {
        height: 150vh;
    }

    .allcars h1 {
        font-size: 1.1rem;
    }

    .allcars span h3 {
        font-size: 0.7rem;
    }

    .allcars-container {
        width: 90%;
        height: 75%;
        background-color: #ff5733;
    }

    .allcarscard {
        width: 42%;
        height: 30%;
        margin: 0.8rem;
    }


    .allcars-1 {
        background-color: aqua;
        width: 100%;
        height: 65%;
    }

    .allcars-2 {
        width: 100%;
        height: 60%;
        background-color: aquamarine;
    }

    .allcars-2 h3 {
        font-size: 0.8rem;
    }

    .allcars-2 p {
        font-size: 0.6rem;
    }

    .allcars-2 h2 {
        font-size: 0.8rem;
    }

    .allcars-button {
        width: 2rem;
        height: 2rem;
        font-size: 1.1rem;
    }

}